body {
  background-size: cover;
  background-attachment: fixed;
  background-image: url(/static/media/background-image.2ebc6bdf.jpg);
}

.container {
  margin: 20px;
  text-align: center;
}

input {
  width: 30%;
  padding: 10px;
  -webkit-padding-after: 10px;
          padding-block-end: 10px;
  border: 5px solid #b0b2b2;
  border-radius: 10px;
  margin: 20px auto;
  color: #b0b2b2;

}

input:focus {
  outline: none;
  box-shadow: 0 0 8px 0 dodgerblue;
}

::-webkit-input-placeholder {
  color: #b0b2b2;
}

::-ms-input-placeholder {
  color: #b0b2b2;
}

::placeholder {
  color: #b0b2b2;
}

.input {
  margin: 30px
}

.post {
